import React, { useState, useEffect } from "react";
import HeaderPlaceholder from "../components/HeaderPlaceholder";

/**
 * api   .
 */
export default function FindStore() {
  //API  
  let [keyword, setKeyword] = useState(" ");

  useEffect(() => {
    navigator.geolocation.getCurrentPosition(function (position) {
      var lat = position.coords.latitude, // 
        lon = position.coords.longitude; // 
    });
    //   
    var markers = [];

    var mapContainer = document.getElementById("map"), //   div
      mapOption = {
        center: new window.kakao.maps.LatLng(37, 126), //  
        level: 1, //   
      };

    //  
    var map = new window.kakao.maps.Map(mapContainer, mapOption);

    //    
    var ps = new window.kakao.maps.services.Places();

    //          
    var infowindow = new window.kakao.maps.InfoWindow({ zIndex: 1 });

    //   
    searchPlaces();

    //    
    function searchPlaces() {
      ps.keywordSearch(keyword, placesSearchCB);
    }

    //      
    function placesSearchCB(data, status, pagination) {
      if (status === window.kakao.maps.services.Status.OK) {
        //   
        //    
        displayPlaces(data);

        //   
        displayPagination(pagination);
      } else if (status === window.kakao.maps.services.Status.ZERO_RESULT) {
        // alert("   .");
        return;
      } else if (status === window.kakao.maps.services.Status.ERROR) {
        alert("    .");
        return;
      }
    }

    //      
    function displayPlaces(places) {
      var listEl = document.getElementById("placesList"),
        menuEl = document.getElementById("menu_wrap"),
        fragment = document.createDocumentFragment(),
        bounds = new window.kakao.maps.LatLngBounds(),
        listStr = "";

      //      
      removeAllChildNods(listEl);

      //     
      removeMarker();

      for (var i = 0; i < places.length; i++) {
        //    
        var placePosition = new window.kakao.maps.LatLng(
            places[i].y,
            places[i].x
          ),
          marker = addMarker(placePosition, i),
          itemEl = getListItem(i, places[i]); //    Element 
        //       
        // LatLngBounds   
        window.kakao.maps.event.addListener(marker, "click", function () {
          console.log("clicked");
        });
        bounds.extend(placePosition);

        //    mouseover 
        //     
        // mouseout    
        (function (marker, title) {
          window.kakao.maps.event.addListener(marker, "mouseover", function () {
            displayInfowindow(marker, title);
          });

          window.kakao.maps.event.addListener(marker, "mouseout", function () {
            infowindow.close();
          });

          itemEl.onmouseover = function () {
            displayInfowindow(marker, title);
          };

          itemEl.onmouseout = function () {
            infowindow.close();
          };
        })(marker, places[i].place_name);

        fragment.appendChild(itemEl);
      }

      //     Element 
      listEl.appendChild(fragment);
      menuEl.scrollTop = 0;

      //       
      map.setBounds(bounds);
    }

    //   Element  
    function getListItem(index, places) {
      var el = document.createElement("li"),
        itemStr =
          '<span className="markerbg marker_' +
          (index + 1) +
          '"></span>' +
          '<div className="info">' +
          "   <h5>" +
          places.place_name +
          "</h5>";

      if (places.road_address_name) {
        itemStr +=
          "    <span>" +
          places.road_address_name +
          "</span>" +
          '   <span className="jibun gray">' +
          places.address_name +
          "</span>";
      } else {
        itemStr += "    <span>" + places.address_name + "</span>";
      }

      itemStr += '<span className="tel">' + places.phone + "</span>" + "</div>";

      el.innerHTML = itemStr;
      el.classNameName = "item";

      return el;
    }

    //       
    function addMarker(position, idx, title) {
      var imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/marker_number_blue.png", //   url,   
        imageSize = new window.kakao.maps.Size(36, 37), //   
        imgOptions = {
          spriteSize: new window.kakao.maps.Size(36, 691), //   
          spriteOrigin: new window.kakao.maps.Point(0, idx * 46 + 10), //       
          offset: new window.kakao.maps.Point(13, 37), //      
        },
        markerImage = new window.kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imgOptions
        ),
        marker = new window.kakao.maps.Marker({
          position: position, //  
          image: markerImage,
        });

      marker.setMap(map); //    
      markers.push(marker); //    

      return marker;
    }

    //       
    function removeMarker() {
      for (var i = 0; i < markers.length; i++) {
        markers[i].setMap(null);
      }
      markers = [];
    }

    //      
    function displayPagination(pagination) {
      var paginationEl = document.getElementById("pagination"),
        fragment = document.createDocumentFragment(),
        i;

      //    
      while (paginationEl.hasChildNodes()) {
        paginationEl.removeChild(paginationEl.lastChild);
      }

      for (i = 1; i <= pagination.last; i++) {
        var el = document.createElement("a");
        el.href = "#";
        el.innerHTML = i;

        if (i === pagination.current) {
          el.classNameName = "on";
        } else {
          el.onclick = (function (i) {
            return function () {
              pagination.gotoPage(i);
            };
          })(i);
        }

        fragment.appendChild(el);
      }
      paginationEl.appendChild(fragment);
    }

    //        
    //   
    function displayInfowindow(marker, title) {
      var content = '<div style="padding:5px;z-index:1;">' + title + "</div>";

      infowindow.setContent(content);
      infowindow.open(map, marker);
    }

    //    Element  
    function removeAllChildNods(el) {
      while (el.hasChildNodes()) {
        el.removeChild(el.lastChild);
      }
    }
  });

  return (
    <>
      <HeaderPlaceholder></HeaderPlaceholder>
      <div className="map_wrap">
        <div id="map"></div>
        <div id="menu_wrap" className="bg_white">
          <div className="select-wrap">
            <label htmlFor="selectlocation"> </label>
            <select
              name="location"
              id="selectlocation"
              onChange={(e) => {
                setKeyword(" " + e.target.value);
              }}
            >
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
              <option value=""></option>
            </select>
          </div>
          <div className="option">
            <form
              onSubmit={() => {
                "searchPlaces(); return false;";
              }}
            >
              <input
                type="text"
                id="keyword"
                size="15"
                value={keyword}
                onChange={(e) => {
                  setKeyword(e.target.value);
                }}
              />
              <button type="submit"></button>
            </form>
          </div>
          <ul id="placesList"></ul>
          <div id="pagination"></div>
        </div>
      </div>
    </>
  );
}
