import React from "react";
import styled from "styled-components";
import { Swiper, SwiperSlide } from "swiper/react";
import { Navigation, Pagination, Scrollbar, A11y, Autoplay, Mousewheel } from 'swiper';
import { Link } from "react-router-dom";

export default function MainBanner() {
  const events = [
    {
      name: "   ",
      description: "      ",
      ImgSrc: "../image/mainBanner00.jpg",
      link : "crown"
    },
    {
      name: " ",
      description: "5   , 1+1  ",
      ImgSrc: "../image/mainBanner01.jpg",
      link : "autocharge"
    },
    {
      name: "BeanInfo",
      description: "     ",
      ImgSrc: "../image/mainBanner02.jpg",
    },
  ];
  return (
    <div className="main-banner">
      <Swiper
        slidesPerView={2}
        spaceBetween={500}
        mousewheel={true}
        modules={[Mousewheel, Pagination]}
      >
        {events.map((data, i) => {
          return (
            <SwiperSlide key={i}>
              <Link to ={`/event/${events[i].link}`}>
              <div className="main-banner-item" key={i}>
                <img src={events[i].ImgSrc} alt="" />
                <article>
                  <h1>{events[i].name}</h1>
                  <p>{events[i].description}</p>
                </article>
              </div>
              </Link>
            </SwiperSlide>
          );
        })}
      </Swiper>
    </div>
  );
}
