import React from "react";
import { useParams } from "react-router-dom";
import SubBG from "../components/SubBG";

export default function SubEvent() {
  const bg = "../image/eventSubVisual.jpg"
  const title = "EVENT/NEWS"

  let { id } = useParams();

  let eventList = [
    {
      name: "crown",
      title: "[]    ",
      imgSrc: "../image/crownEvent.jpg",
    },
    {
      name: "halloween",
      title: "[]   3",
      imgSrc: "../image/halloweenEvent.jpg",
    
    },
    {
      name: "autocharge",
      title: "[] 5    1+1 ",
      imgSrc: "../image/autoChargeEvent.jpg",
    },
    {
      name: "teaandPistachio",
      title: "Tea & Pistachio",
      imgSrc: "../image/teapistachio.jpg",
    },
  ];

  
  const select = eventList.filter((x) => x.name === id);

  return (
    <>
      <SubBG bg={bg} title={title}></SubBG>
      <div className="sub-wrap">
        <p>{select[0].title}</p>
        <figure>
          <img src={select[0].imgSrc} alt="" />
        </figure>
      </div>
    </>
  );
}
