import React from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import { Navigation, Pagination, Scrollbar, A11y, Autoplay } from 'swiper';

export default function BrandPrinciple() {
  const slideItems = []
  for(let i = 1; i <= 10; i++) {
    slideItems.push(`../image/principlesSlide${i}.jpg`)
  }
  console.log(slideItems)

  return (
    <div className="brand-history">
      <section className="principle-title">
        <h1>    </h1>
        <span>QUALITY NEVER COMPROMISED</span>
        <p>
                   
              .     
           Seed to Cup    , , , ,
                ,  
                   
           .
        </p>
      </section>
      <section className="principle-slide">
        <img src="../image/slideBg01.jpg" alt="" />
        <Swiper
      navigation={true}
      spaceBetween={30}
      autoplay={{
        delay: 2500,
        disableOnInteraction: false,
      }}
      rewind={true}
      loop={true}
      pagination={{
        type: "fraction",
      }}
      modules={[Navigation, Pagination, Scrollbar, A11y, Autoplay]}>
          {
            slideItems.map((data,i)=>{
              return(
              <SwiperSlide key={i}>
                <img src={slideItems[i]} alt="" />
              </SwiperSlide>
              )  
            })
          }
        </Swiper>
        <img src="../image/slideBg02.jpg" alt="" />
      </section>
    </div>
  );
}
